<template>
  <div class="search">
    <div class="search-search">
      <div class="back" @click="back">
        <van-icon color="#0C34BA" size="20px" name="arrow-left" />
      </div>
      <van-search v-model="value" shape="round" placeholder="搜索" @search="search" />
    </div>
    <div class="product-list">
      <div class="menu-product">
        <div
          class="pro-list"
          @click="proDetails(item.pid)"
          v-for="(item, index) in productsData"
          :key="index"
        >
          <div class="pro-img">
            <img class="autoh-img" :src="item.smallImg" alt />
          </div>
          <div class="pro-info">
            <div class="pro-text fl">
              <div class="ch-name">{{item.name}}</div>
              <div class="en-name">{{item.enname}}</div>
              <div class="pro-price">￥{{item.price}}</div>
            </div>
            <div class="pro-add fr">
              <van-icon name="plus" color="#fff" />
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import "../assets/css/search.less";
export default {
  data() {
    return {
      productsData: [],
      value: "",
    };
  },
  created() {
    this.getProductsData(this.$route.query);
  },
  methods: {
    back() {
      this.$router.go(-1);
    },
    getProductsData(value) {
      if (typeof value != "string") return;

      // 数据加载等待动画toast
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/search",
        params: {
          appkey: this.appkey,
          name: value,
        },
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code == "Q001") {
            this.productsData = result.data.result;
             
          }
        })
        .catch((err) => {
           
        });
    },

    // 商品点击事件
    proDetails(pid) {
      this.$router.push({ name: "Details", query: { pid } });
    },

    // 搜索事件
    search() {
       
      this.getProductsData(this.value);
    },
  },
};
</script>
